.info-card {
	background: #2b3250;
	position: fixed;
	width: 320px;
	height: calc(100% - 58px);
	left: -320px;
	bottom: 0;
	color: #fff;
	z-index: 2;
	box-shadow: 0px 0px 40px 16px rgba(11, 16, 40, 0.94);
	overflow: auto;
	transition: left 150ms;
}

.info-card.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.info-card__show {
	position: absolute;
	color: #859DE0;
	font-size: 12px;
	transform: rotate(-90deg);
	right: -5px;
	top: 76px;
	cursor: pointer;
	display: none;
}

.info-card.open {
	left: 0;
}

.info-card.open.collapsed {
	left: -267px;
}

.info-card.open.collapsed .info-card__show {
	display: block;
}

.info-card.open.collapsed .info-card__close {
	transform: rotate(180deg);
}

.info-card__header {
	background: #29367A;
	padding: 24px;
}

.info-card.light .info-card__header {
	background: #fff;
}

.info-card__subtitle {
	font-size: 14px;
	color: #859DE0;
	font-weight: bold;
}

.info-card__title {
	font-size: 20px;
	text-transform: uppercase;
	line-height: 28px;
	font-weight: 500;
	margin-top: 8px;
}

.info-card__section {
	padding: 24px 24px 0;
}

.info-card__section-title {
	color: #5872DA;
	font-size: 20px;
	font-weight: 500;
	line-height: 28px;
	border-bottom: 1px solid rgba(88, 114, 218, 0.2);
}

.info-card__item {
	margin-top: 16px;
	font-size: 14px;
	line-height: 18px;
}

.info-card__item-title {
	color: #859DE0;
}

.info-card__info {
	background: #5872DA;
	padding: 16px 24px 8px;
	font-size: 14px;
}

.info-card.light .info-card__info {
	background: #e6e9f9;
}

.info-card__info-item {
	margin-bottom: 8px;
}

.info-card__info-item>span {
	opacity: 0.6;
	margin-right: 4px;
}

.info-card__button {
	width: 100%;
	height: 64px;
	color: #fff;
	text-align: center;
	text-decoration: none;
	border-top: 1px solid #181E2F;
	border-bottom: 1px solid #181E2F;
	margin-top: 30px;
	display: block;
	left: 0;
	bottom: 0;
	line-height: 64px;
	font-size: 14px;
	background: #2b3250;
}

.info-card.light .info-card__button {
	border-top: 1px solid #d6dbf3;
	border-bottom: 1px solid #d6dbf3;
	background: #fff;
	color: #2b3250;
}

.info-card__button:hover {
	background: rgba(255, 255, 255, 0.07);
}

.info-card.light .info-card__button:hover {
	background: #d6dbf3;
}

.info-card__close {
	position: absolute;
	top: 21px;
	right: 16px;
	cursor: pointer;
	fill: #5872da;
}