.right-menu {
	background: #2b3250;
	position: absolute;
	color: #fff;
	right: 16px;
	top: 94px;
	padding: 8px;
	width: 56px;
	border-radius: 8px;
	z-index: 100;
	max-height: 333px;
	overflow: hidden;
	box-sizing: border-box;
}

.right-menu.light {
	background: #fff;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.right-menu__item {
	width: 40px;
	height: 40px;
	text-align: center;
	line-height: 40px;
	font-weight: bold;
	cursor: pointer;
	border-radius: 6px;
	/*opacity: 0.5;*/
	font-size: 18px;
	margin-bottom: 6px;
	 display: flex;
    justify-content: center;
    align-items: center;
}

.right-menu__item.active {
	opacity: 1;
}

.right-menu__item:hover {
	background: rgba(255, 255, 255, 0.1);
}

.light .right-menu__item:hover {
	background: rgba(41, 44, 67, 0.1);
}

.right-menu__more {
	position: absolute;
	left: 8px;
	bottom: 3px;
	background: #2b3250;
}

.right-menu__more:hover {
	background: #424a6b;
}

.light .right-menu__more {
	background: #fff;
}

.light .right-menu__more:hover {
	background: #e9e9ec;
}

.right-menu.collapsed {
	max-height: 10000px;
}

.right-menu.collapsed .right-menu__more {
	display: none;
}