.risk-menu {
	position: absolute;
	width: 340px;
	height: 240px;
	left: 50%;
	margin-left: -170px;
	bottom: 20px;
	background: #2B3250;
	box-shadow: 0px 16px 40px rgba(11, 16, 40, 0.94);
	border-radius: 8px;
	z-index: 100;
	color: #fff;
	font-size: 14px;
	cursor: move;
}

.risk-menu.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.risk-menu.close {
    display: none;
} 

.risk-menu:after {
	content: '';
	position: absolute;
	width: 6px;
	height: 200px;
	left: -3px;
	top: 20px;
	background: #F2696D;
	border-radius: 8px;
	z-index: 2;
}

.risk-menu__header {
	display: flex;
	align-items: center;
	font-weight: bold;
}

.risk-menu__image img {
	width: 80px;
	height: 80px;
}

.risk-menu__items {
	border-top: 1px solid rgba(88, 114, 218, 0.2);
}

.risk-menu__item {
	padding: 12px 24px;
	cursor: pointer;
	border-bottom: 1px solid rgba(88, 114, 218, 0.2);
}

.risk-menu__item:hover {
	background: rgba(88, 114, 218, 0.2);
}

.risk-menu__close {
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}

.risk {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background: #2B3250;
	border-radius: 8px;
	z-index: 1;
	color: #fff;
}

.risk-menu.light .risk{
	background: #fff;
	color: #2b3250;
}

.risk.close{
	display: none;
}

.risk__info {
	padding-right: 50px;
}

.risk__header {
	display: flex;
	align-items: center;
	font-weight: bold;
}

.risk__image img {
	width: 80px;
	height: 80px;
}

.risk__close {
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}

.risk__inn {
	background: rgba(88, 114, 218, 0.2);
	padding: 11px 24px;
}

.risk__inn>span {
	color: #859DE0;
	margin-right: 5px;
}

.risk__rntps {
	padding: 0 24px;
}

.risk__rntps-title {
	color: #859DE0;
	font-weight: bold;
	margin-top: 16px;
	margin-bottom: 8px;
}

.risk__rntps-item {
	font-weight: 500;
	margin-bottom: 4px;
	letter-spacing: 1px;
}