.zoom {
	position: fixed;
	width: 320px;
	height: 56px;
	z-index: 4;
	bottom: 8px;
	right: 8px;
	background: #2b3250;
	display: flex;
	padding: 8px;
	border-radius: 8px;
	box-sizing: border-box;
}

.zoom.light {
	background: #fff;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.zoom.map__open {
	border-radius: 0 0 8px 8px;
	    width: 280px;
}

.zoom.map__open .zoom__mapicon {
	display: none;
}

.zoom__minus,
.zoom__plus,
.zoom__mapicon {
	width: 40px;
	height: 40px;
	border-radius: 8px;
	cursor: pointer;
}

.zoom__minus:hover,
.zoom__plus:hover,
.zoom__mapicon:hover {
	background: rgba(255, 255, 255, 0.1);
}

.zoom.light .zoom__minus:hover,
.zoom.light .zoom__plus:hover,
.zoom.light .zoom__mapicon:hover {
	background: rgba(41, 44, 67, 0.1);
}

.zoom__mapicon {
	
}

.map__close {
	position: absolute;
	right: 0;
	top: 0;
	cursor: pointer;
}

.zoom__scale {
	width: 168px;
	margin: 0 8px;
	padding: 12px 0;
}

.input-range__label--min {
    display: none;
}

.input-range__label--max {
    display: none;
}

.input-range__label--value {
    display: none;
}

.input-range__slider {
    width: 11px;
    height: 11px;
    margin-top: -7px;
    background: #859DE0;
    border: none;
}

.input-range__track {
    height: 3px;
    background: #5A6487;
}

.input-range__track.input-range__track--active {
    background: #859DE0;
}

.input-range__slider:active {
    transform: scale(1);
}

.zoom__map {
    z-index: 100;
    position: absolute;
    overflow: hidden;
    bottom: 64px;
    right: 8px;
    width: 280px;
    height: 145px;
    background: #2b3250;
    padding: 15px 15px 0 15px;
    border-radius: 8px 8px 0 0;
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
}

.zoom__map.map__open{
	visibility: visible;
    opacity: 1;
}

.zoom__map.light{
	background: #fff;
	border-color: #fff;
}

.map__close {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
}

.map__overview{
	width: 215px;
	height: 130px;
	background: #1d2336;
}

.zoom__map.light .map__overview{
	background: #eef0f5;
}