@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,700&display=swap&subset=cyrillic);

.header {
	background: #2B3250;
	height: 58px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	z-index: 3;
	font-size: 14px;
	padding: 0 12px;
}

.header.light{
	background: #fff;
}

.header__left,
.header__right {
	display: flex;
	align-items: center;
}

.header__item {
	margin: 0 4px;
	height: 40px;
	padding: 0 10px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 6px;
	cursor: pointer;
}

.header__item:hover {
	background: rgba(255, 255, 255, 0.1);
}

.header.light .header__item:hover {
	background: rgba(41, 44, 67, 0.1);
}

.header__item.active {
	background: rgba(255, 255, 255, 0.15);
	cursor: default;
}

.header.light .header__item.active {
	background: rgba(41, 44, 67, 0.15);
}

.header__item input {
	background: transparent;
	font-family: 'Fira Sans';
	color: #fff;
	font-size: 14px;
	border: none;
	width: 23px;
}

.light .header__item input {
	color: #2b3250;
}

.header__item-zoom:after {
	content: '%';
}
.right-menu {
	background: #2b3250;
	position: absolute;
	color: #fff;
	right: 16px;
	top: 94px;
	padding: 8px;
	width: 56px;
	border-radius: 8px;
	z-index: 100;
	max-height: 333px;
	overflow: hidden;
	box-sizing: border-box;
}

.right-menu.light {
	background: #fff;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.right-menu__item {
	width: 40px;
	height: 40px;
	text-align: center;
	line-height: 40px;
	font-weight: bold;
	cursor: pointer;
	border-radius: 6px;
	/*opacity: 0.5;*/
	font-size: 18px;
	margin-bottom: 6px;
	 display: flex;
    justify-content: center;
    align-items: center;
}

.right-menu__item.active {
	opacity: 1;
}

.right-menu__item:hover {
	background: rgba(255, 255, 255, 0.1);
}

.light .right-menu__item:hover {
	background: rgba(41, 44, 67, 0.1);
}

.right-menu__more {
	position: absolute;
	left: 8px;
	bottom: 3px;
	background: #2b3250;
}

.right-menu__more:hover {
	background: #424a6b;
}

.light .right-menu__more {
	background: #fff;
}

.light .right-menu__more:hover {
	background: #e9e9ec;
}

.right-menu.collapsed {
	max-height: 10000px;
}

.right-menu.collapsed .right-menu__more {
	display: none;
}
.risk-menu {
	position: absolute;
	width: 340px;
	height: 240px;
	left: 50%;
	margin-left: -170px;
	bottom: 20px;
	background: #2B3250;
	box-shadow: 0px 16px 40px rgba(11, 16, 40, 0.94);
	border-radius: 8px;
	z-index: 100;
	color: #fff;
	font-size: 14px;
	cursor: move;
}

.risk-menu.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.risk-menu.close {
    display: none;
} 

.risk-menu:after {
	content: '';
	position: absolute;
	width: 6px;
	height: 200px;
	left: -3px;
	top: 20px;
	background: #F2696D;
	border-radius: 8px;
	z-index: 2;
}

.risk-menu__header {
	display: flex;
	align-items: center;
	font-weight: bold;
}

.risk-menu__image img {
	width: 80px;
	height: 80px;
}

.risk-menu__items {
	border-top: 1px solid rgba(88, 114, 218, 0.2);
}

.risk-menu__item {
	padding: 12px 24px;
	cursor: pointer;
	border-bottom: 1px solid rgba(88, 114, 218, 0.2);
}

.risk-menu__item:hover {
	background: rgba(88, 114, 218, 0.2);
}

.risk-menu__close {
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}

.risk {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background: #2B3250;
	border-radius: 8px;
	z-index: 1;
	color: #fff;
}

.risk-menu.light .risk{
	background: #fff;
	color: #2b3250;
}

.risk.close{
	display: none;
}

.risk__info {
	padding-right: 50px;
}

.risk__header {
	display: flex;
	align-items: center;
	font-weight: bold;
}

.risk__image img {
	width: 80px;
	height: 80px;
}

.risk__close {
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}

.risk__inn {
	background: rgba(88, 114, 218, 0.2);
	padding: 11px 24px;
}

.risk__inn>span {
	color: #859DE0;
	margin-right: 5px;
}

.risk__rntps {
	padding: 0 24px;
}

.risk__rntps-title {
	color: #859DE0;
	font-weight: bold;
	margin-top: 16px;
	margin-bottom: 8px;
}

.risk__rntps-item {
	font-weight: 500;
	margin-bottom: 4px;
	letter-spacing: 1px;
}
.info-card {
	background: #2b3250;
	position: fixed;
	width: 320px;
	height: calc(100% - 58px);
	left: -320px;
	bottom: 0;
	color: #fff;
	z-index: 2;
	box-shadow: 0px 0px 40px 16px rgba(11, 16, 40, 0.94);
	overflow: auto;
	-webkit-transition: left 150ms;
	transition: left 150ms;
}

.info-card.light {
	background: #fff;
	color: #2b3250;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.info-card__show {
	position: absolute;
	color: #859DE0;
	font-size: 12px;
	-webkit-transform: rotate(-90deg);
	        transform: rotate(-90deg);
	right: -5px;
	top: 76px;
	cursor: pointer;
	display: none;
}

.info-card.open {
	left: 0;
}

.info-card.open.collapsed {
	left: -267px;
}

.info-card.open.collapsed .info-card__show {
	display: block;
}

.info-card.open.collapsed .info-card__close {
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
}

.info-card__header {
	background: #29367A;
	padding: 24px;
}

.info-card.light .info-card__header {
	background: #fff;
}

.info-card__subtitle {
	font-size: 14px;
	color: #859DE0;
	font-weight: bold;
}

.info-card__title {
	font-size: 20px;
	text-transform: uppercase;
	line-height: 28px;
	font-weight: 500;
	margin-top: 8px;
}

.info-card__section {
	padding: 24px 24px 0;
}

.info-card__section-title {
	color: #5872DA;
	font-size: 20px;
	font-weight: 500;
	line-height: 28px;
	border-bottom: 1px solid rgba(88, 114, 218, 0.2);
}

.info-card__item {
	margin-top: 16px;
	font-size: 14px;
	line-height: 18px;
}

.info-card__item-title {
	color: #859DE0;
}

.info-card__info {
	background: #5872DA;
	padding: 16px 24px 8px;
	font-size: 14px;
}

.info-card.light .info-card__info {
	background: #e6e9f9;
}

.info-card__info-item {
	margin-bottom: 8px;
}

.info-card__info-item>span {
	opacity: 0.6;
	margin-right: 4px;
}

.info-card__button {
	width: 100%;
	height: 64px;
	color: #fff;
	text-align: center;
	text-decoration: none;
	border-top: 1px solid #181E2F;
	border-bottom: 1px solid #181E2F;
	margin-top: 30px;
	display: block;
	left: 0;
	bottom: 0;
	line-height: 64px;
	font-size: 14px;
	background: #2b3250;
}

.info-card.light .info-card__button {
	border-top: 1px solid #d6dbf3;
	border-bottom: 1px solid #d6dbf3;
	background: #fff;
	color: #2b3250;
}

.info-card__button:hover {
	background: rgba(255, 255, 255, 0.07);
}

.info-card.light .info-card__button:hover {
	background: #d6dbf3;
}

.info-card__close {
	position: absolute;
	top: 21px;
	right: 16px;
	cursor: pointer;
	fill: #5872da;
}
.zoom {
	position: fixed;
	width: 320px;
	height: 56px;
	z-index: 4;
	bottom: 8px;
	right: 8px;
	background: #2b3250;
	display: flex;
	padding: 8px;
	border-radius: 8px;
	box-sizing: border-box;
}

.zoom.light {
	background: #fff;
	box-shadow: 0px 16px 40px rgba(90, 100, 135, 0.24);
}

.zoom.map__open {
	border-radius: 0 0 8px 8px;
	    width: 280px;
}

.zoom.map__open .zoom__mapicon {
	display: none;
}

.zoom__minus,
.zoom__plus,
.zoom__mapicon {
	width: 40px;
	height: 40px;
	border-radius: 8px;
	cursor: pointer;
}

.zoom__minus:hover,
.zoom__plus:hover,
.zoom__mapicon:hover {
	background: rgba(255, 255, 255, 0.1);
}

.zoom.light .zoom__minus:hover,
.zoom.light .zoom__plus:hover,
.zoom.light .zoom__mapicon:hover {
	background: rgba(41, 44, 67, 0.1);
}

.zoom__mapicon {
	
}

.map__close {
	position: absolute;
	right: 0;
	top: 0;
	cursor: pointer;
}

.zoom__scale {
	width: 168px;
	margin: 0 8px;
	padding: 12px 0;
}

.input-range__label--min {
    display: none;
}

.input-range__label--max {
    display: none;
}

.input-range__label--value {
    display: none;
}

.input-range__slider {
    width: 11px;
    height: 11px;
    margin-top: -7px;
    background: #859DE0;
    border: none;
}

.input-range__track {
    height: 3px;
    background: #5A6487;
}

.input-range__track.input-range__track--active {
    background: #859DE0;
}

.input-range__slider:active {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.zoom__map {
    z-index: 100;
    position: absolute;
    overflow: hidden;
    bottom: 64px;
    right: 8px;
    width: 280px;
    height: 145px;
    background: #2b3250;
    padding: 15px 15px 0 15px;
    border-radius: 8px 8px 0 0;
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
}

.zoom__map.map__open{
	visibility: visible;
    opacity: 1;
}

.zoom__map.light{
	background: #fff;
	border-color: #fff;
}

.map__close {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
}

.map__overview{
	width: 215px;
	height: 130px;
	background: #1d2336;
}

.zoom__map.light .map__overview{
	background: #eef0f5;
}
body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans';
}

* {
  outline: none;
}

.app{
  background: #1d2336;
  color: #fff;
  fill: #fff;
  height: 100%;
  width: 100%;
  position: absolute;
  overflow: hidden;
}

.app.light{
  background: #fff;
  color: #2b3250;
  fill: #2b3250;
}

.diagram-component {
  background: #1d2336;
  position: absolute;
  top: 58px;
  bottom: 0;
  left: 0;
  right: 0;
}

.light .diagram-component {
  background: #eef0f5;
}

.Dropdown-control {
    padding: 10px 42px 10px 20px;
    border: none;
    background: transparent;
    color: #fff;
    cursor: pointer;
    border-radius: 8px;
}

.light .Dropdown-control {
    color: #2b3250;
}

.Dropdown-control:hover {
    background: rgba(255, 255, 255, 0.1);
}

.Dropdown-root.is-open .Dropdown-control {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px 8px 0 0;
}

.light .Dropdown-control:hover {
    background: rgba(41, 44, 67, 0.1);
}

.light .Dropdown-root.is-open .Dropdown-control {
    background: rgba(41, 44, 67, 0.1);
}

.Dropdown-menu {
    background: #29367A;
    color: #fff;
    border: none;
    border-radius: 0 0 8px 8px;
    padding: 12px 10px;
    width: auto;
    min-width: 100%;
}

.Dropdown-option {
    white-space: nowrap;
    padding: 8px 10px;
    border-radius: 6px;
    cursor: pointer;
    margin-bottom: 5px;
    color: #fff;
}

.Dropdown-option:hover {
    background: rgba(255, 255, 255, 0.1);
    color: #fff;
}

.Dropdown-option.is-selected {
    background: #5872DA;
    cursor: default;
    color: #fff;
}

.light .Dropdown-menu {
    background: #f2f4f7;
    color: #2b3250;
}

.light .Dropdown-option {
    color: #2b3250;
}

.light .Dropdown-option.is-selected {
    background: rgba(41, 44, 67, 0.1);
    color: #2b3250;
}

.light .Dropdown-option:hover {
    background: rgba(41, 44, 67, 0.1);
    color: #2b3250;
}

.Dropdown-option:last-child {
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}

.Dropdown-arrow-wrapper {
    position: absolute;
    top: 16px;
    right: 20px;
    height: 7px;
    width: 10px;
    line-height: 0;
    -webkit-transition: -webkit-transform 100ms;
    transition: -webkit-transform 100ms;
    transition: transform 100ms;
    transition: transform 100ms, -webkit-transform 100ms;
}

.is-open .Dropdown-arrow-wrapper {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
